  position: relative;
}
.task-group .task-change-position {
  position: relative;
  top: 5px;
  width: 20px;
  height: 16px;
  display: inline-block;
  background-size: 20px 16px;
  background-image: url("../img/icon/order.svg");
  cursor: pointer;
  vertical-align: top;
  opacity: 0;
}
.task-group:hover .task-check-circle {
  transform: scale(1.35, 1.35);
}
.task-group .task-check {
  position: relative;
  top: 2px;
  display: inline-block;
  width: 10px;
  height: 10px;
  z-index: 3;
  vertical-align: top;
}
.task-group .task-check > .task-check-circle {
  position: relative;
  width: 100%;
  height: 100%;
  display: inline-block;
  border-radius: 50%;
  transition: all 300ms ease;
}
.task-group .task-check > .task-check-circle.ng-hide {
  transform: scale(0, 0);
}
.task-group .task-check > i {
  position: absolute;
  top: 1px;
  left: -4px;
  color: rgba(161, 166, 173, 0.38);
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 50%;
  transform: scale(1, 1);
}
.task-group .task-check > i.ng-hide-remove {
  transition: all 200ms ease;
  transition-delay: 300ms;
}
.task-group .task-check > i.ng-hide {
  transform: scale(0, 0);
}
.task-group .task-text {
  position: relative;
  display: inline-block;
  width: 80%;
  padding: 2px 5px;
  font-size: 0.95em;
  color: #5e5e73;
