}
.task-group:hover .task-preference {
  visibility: visible;
}
.task-group .task-preference {
  position: absolute;
  top: 0;
  right: 10px;
  display: inline-block;
  visibility: hidden;
  width: 16px;
  height: 16px;
  margin-top: 6px;
  cursor: pointer;
}
.task-group .task-preference:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/icon/preference.svg");
