  font-size: 1.1em;
  color: #fff;
  background-color: #2dcc70;
  border: 2px solid #2dcc70;
}

.user-interest__button--selected-lgbt-indigo {
  font-size: 1.1em;
  color: #fff;
  background-color: #3598db;
  border: 2px solid #3598db;
}

.user-interest__button--selected-lgbt-violet {
  font-size: 1.1em;
  color: #fff;
  background-color: #9a59b3;
  border: 2px solid #9a59b3;
}

.user-interest__selected-close {
  font-size: 0.7em;
  vertical-align: middle;
  margin-left: 5px;
}

.user-interest__wrapper-main-button {
  margin-top: 20px;
  float: right;
}

.user-interest__button--save {
  font-size: 1.2em;
  color: #fff;
  background-color: #7ec58d;
  border: 2px solid #7ec58d;
}

.user-interest__button--cancel {
  font-size: 1.2em;
  color: #5e5e73;
  background-color: #fff;
  border: 2px solid #5e5e73;
}

@keyframes chooseInterest {
  20% {
    transform: scale(1.2, 1.2);
  }
  80% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(0, 0);
  }
}
@keyframes shake {
  0%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  10%, 30%, 50%, 70%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
  }
  20%, 40%, 60%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }
}
/* bootstrap !important */
.navbar {
  height: 50px;
  border-bottom: 1px solid #292c3b;
  background-color: #292c3b;
}

.navbar__header {
  height: 100%;
  display: flex;
  align-items: center;
  float: left;
}

.navbar__brand {
  display: block;
  width: 26px;
  height: 44px;
}

.navbar__image {
  width: 100%;
  height: 100%;
}

.navbar__auth {
  height: 100%;
  display: block;
  float: right;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navbar__list {
  display: block;
  float: left;
}

.navbar__link {
  display: block;
  font-size: 0.9em;
  padding: 15px;
  color: #acb1be;
}
.navbar__link:link, .navbar__link:visited {
  color: #acb1be;
}
.navbar__link:hover, .navbar__link:active {
  color: #fff;
}

.navbar__links {
  display: flex;
  align-items: center;
  float: left;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navbar__link--firstLink {
  display: block;
  font-size: 0.8em;
  padding: 15px 15px 15px 0;
  color: #acb1be;
}
.navbar__link--firstLink:link, .navbar__link--firstLink:visited {
  color: #acb1be;
}
.navbar__link--firstLink:hover, .navbar__link--firstLink:active {
  color: #fff;
}

.done-button {
  width: 100%;
  height: 3em;
  font-size: 0.9em;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  border: 0;
  background-color: #7ec58d;
}

.userNavbar {
  position: relative;
  background-color: #292c3b;
  /*background-color: #2b2e36;*/
}

.userData {
  display: flex;
  align-items: center;
  float: right;
}

.userData__avatar {
  position: relative;
  width: 38px;
  height: 38px;
  display: inline-block;
  cursor: pointer;
}

.userData__avatarIcon {
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: rgba(48, 50, 61, 0.5);
  background-image: url("../img/icon/edit-avatar.svg");
  background-position: center center;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  opacity: 0;
  border-radius: 50%;
  transition: opacity 300ms ease;
}
.userData__avatar:hover .userData__avatarIcon {
  opacity: 1;
}

.userData__avatarImage {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.userData__uploadAvatar {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
}

.userData__link {
  display: block;
  font-size: .9em;
  padding: 15px;
  color: #acb1be;
  cursor: pointer;
}
.userData__link:active, .userData__link:hover {
  color: #fff;
}
.userData__link:link, .userData__link:visited {
  color: #acb1be;
}

.userData__caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 4px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.userData__dropdown {
  position: absolute;
  right: 0;
  display: block;
  min-width: 150px;
  max-height: 0;
  z-index: 9;
  padding: 0 2px 0 25px;
  overflow: hidden;
  opacity: 0;
  background: #fff;
  border-radius: 0px;
  transition: all 400ms cubic-bezier(0.7, 0, 0.3, 1);
}
.userData__dropdown--open .userData__dropdown {
  max-height: 500px;
  opacity: 1;
  box-shadow: 0 0 1px rgba(76, 86, 103, 0.25), 0 2px 18px rgba(31, 37, 50, 0.32);
}

.userData__dropdownLinks {
  padding: 0;
  margin: 12px 0 15px;
  transform: translate3d(0, -20px, 0);
  transition: opacity 400ms ease, transform 400ms cubic-bezier(0.7, 0, 0.3, 1);
  list-style: none;
}
.userData__dropdown--open .userData__dropdownLinks {
  transform: translate3d(0, 0, 0);
}

.userData__dropdownList {
  display: inline-block;
  padding: 4px 0;
  font-size: 13px;
  text-decoration: none;
  color: #6f7685;
}
.userData__dropdownList:hover {
  color: #333c4e;
}

.popup-new-list {
  position: absolute;
  top: 150px;
  left: 0;
  right: 0;
  width: 380px;
  margin: auto;
  padding: 15px 30px 20px;
  z-index: 11;
  background: #fff;
  border-radius: 2px;
  opacity: 1;
  transform: scale(1);
}
.popup-new-list.ng-hide-add, .popup-new-list.ng-hide-remove {
  transition: transform .2s, opacity .2s;
}
.popup-new-list.ng-hide {
  opacity: 0;
  transform: scale(0);
}
.popup-new-list h2 {
  margin-bottom: 5%;
  font-size: 1.3em;
  text-align: center;
  color: #464d5d;
  font-weight: 400;
}
.popup-new-list .form-group {
  position: relative;
}
.popup-new-list .error-new-list {
  position: absolute;
  top: 40px;
  font-size: 0.8em;
  color: #f36;
  margin: 0;
}
.popup-new-list .popup-member-title {
  font-size: 0.7em;
  text-transform: uppercase;
  color: #464d5d;
  margin: 0 0 10px;
}
.popup-new-list input {
  width: 100%;
  height: 1em;
  margin: 0 0 20px;
  padding: 19px 18px;
  font-size: 0.9em;
  font-weight: 400;
  color: #464d5d;
  border: 1px solid #DEE3E9;
}
.popup-new-list input:focus {
  border-color: #56b169;
}
.popup-new-list input.has-error {
  border-color: #f36;
}
.popup-new-list .popup-list-member img {
  width: 32px;
  height: 32px;
  border-radius: 50% 50%;
  vertical-align: top;
}
.popup-new-list .popup-list-member-data {
  display: inline-block;
  width: calc(95% - 36px);
  margin: -3px 0 0 10px;
}
.popup-new-list .popup-list-member-data .popup-member-name {
  margin: 0;
  font-size: 0.9em;
  color: #464d5d;
}
.popup-new-list .popup-list-member-data .popup-member-email {
  font-size: 0.8em;
  color: #555e71;
  margin: 0 0 10px;
}
.popup-new-list .popup-close {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 17px;
  height: 17px;
  background-size: 17px 17px;
  background-image: url("../img/icon/plus-popup.svg");
